import storage from 'good-storage'

const STORAGE_MAX_LENTH = 20
const SEARCH_KEY = '_searchData_'

export function getSearchHistory () { // storage
  return storage.get(SEARCH_KEY, [])
}

export function setSearchHistory (val) { // 
  var arr = getSearchHistory()
  insetHistory(arr, val, item => {
    return item === val
  }, STORAGE_MAX_LENTH)
  storage.set(SEARCH_KEY, arr)
  return arr
}

export function deleteSearchHistory (index) { // 
  var arr = getSearchHistory()
  cutOutHistory(arr, index)
  storage.set(SEARCH_KEY, arr)
  return arr
}

export function clearSearchHistory () { // 
  storage.remove(SEARCH_KEY)
  return []
}

function cutOutHistory (arr, index) { // arrvalindexval
  index > -1 ? arr.splice(index, 1) : ''
}

function insetHistory (arr, val, compare, lenMax) { // ,
  let index = arr.findIndex(compare)
  if (index == 0) {
    return
  }
  index > -1 ? arr.splice(index, 1) : ''
  arr.unshift(val)
  lenMax && arr.length > lenMax ? arr.pop() : ''
}
