<template>
    <div class="wrap" >
        <div class="sliderWrap" ref="sliderWrap">
            <div class="slider-group" ref="sliderGroup">
                <slot></slot>
            </div>
            <div class="dots">
                <span class="dot"
                :class="{active: currentPageIndex === index}"
                v-for="(item,index) in dots"
                :key="index"></span>
            </div>
        </div>
    </div>

</template>
<script>
import Bscroll from 'better-scroll'
import addClass from 'common/js/dom'

export default {
  name: 'Slider',
  data () {
    return {
      currentPageIndex: 0, // 
      dots: [] // 
    }
  },
  props: {
    bannerData: Array,
    params: {
      type: Object,
      default: {
        loop: {
          type: Boolean, // 
          default: true
        },
        autoPlay: {
          type: Boolean, // 
          default: true
        },
        interval: {
          type: Number, // 
          default: 3000
        }
      }
    }
  },
  watch: {
    bannerData () {
      this._sliderInit() // domdom
    }
  },
  mounted () {
    // this._sliderInit();   //mountedwatch
  },
  methods: {
    _sliderInit () {
      setTimeout(() => { // dom20dom
        this._setSliderWidth()
        this._setDos()
        this._initBscroll()
        this._play()
        window.addEventListener('resize', () => { // resize
          if (!this.slider) { // 
            return
          }
          this._setSliderWidth()
          this.slider.refresh() // setSliderWidth
        })
      }, 20)
    },
    _setSliderWidth () {
      this.childrens = this.$refs.sliderGroup.children // ,thisdots
      var wrapWidth = this.$refs.sliderWrap.clientWidth // 
      var groupsWidth = this.childrens.length * wrapWidth // 
      for (var i = 0; i < this.childrens.length; i++) {
        var child = this.childrens[i]
        addClass(child, 'slider-item') // class
        child.style.width = wrapWidth + 'px' // 
      }
      if (this.params.loop) { // 
        groupsWidth += 2 * wrapWidth
      }
      this.$refs.sliderGroup.style.width = groupsWidth + 'px' // 
    },
    _setDos () {
      this.dots = new Array(this.childrens.length)
    },
    _initBscroll () {
      this.slider = new Bscroll(this.$refs.sliderWrap, {
        scrollY: false, // y
        scrollX: true, // x
        momentum: false, // 
        click: true,
        // snap:true,//slider
        snap: {
          loop: this.params.loop, // 
          threshold: 0.3, // 
          speed: 600 // 
        }
        // snap:true,
        // snapLoop:this.loop,//
        // snapThreshold:0.3,//
        // snapSpeed:400,//
      })

      this.slider.on('scrollEnd', () => { // better-scroll,
        this.currentPageIndex = this.slider.getCurrentPage().pageX // 
        if (this.params.autoPlay) {
          clearTimeout(this.timer)
          // bugend
          this._play()
        }
      })

      this.slider.on('beforeScrollStart', () => { // 
        if (this.params.autoPlay) {
          clearTimeout(this.timer)
        }
      })
    },
    _play () {
      this.timer = setTimeout(() => {
        this.slider.next()
      }, this.params.interval)
    }
  }
}
</script>

