<template>
    <div class="category-wrap" ref="cateGoryWrap">
        <div class="category">
            <span
            class="item"
            :class="{active : item.fatherNum == fatherShow}"
            v-for="(item, index) in categoryData.fathers"
            :key="index"
            @click="changeFather(item.fatherNum)"
            >{{item.fatherName}}</span>
        </div>
        <div class="childerns" v-show="typeShow">
            <div class="types-wrap">
                <span class="types"
                v-for="(item, index) in categoryData.children" :key="index"
                v-show="item.category == currentFather"
                @click="changeChild(item.name,item.category,item.resourceType)"
                :class="{active : item.name == currentName}"
                >{{item.name}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Category',
  props: {
    categoryData: Object // {fathers:[{fatherNum:xx,fatherName}...],children:[{name:xxx,category:xxx}...]}
  },
  data () {
    return {
      currentFather: -1,
      currentName: null,
      typeShow: true,
      fatherShow: -1,
      oldFaterShow: -1
    }
  },
  mounted () {
    this.changeType()
  },
  activated () {
    document.getElementById('app').addEventListener('touchstart', this.isClickOut, false) // touchstartchlidrens
  },
  deactivated () {
    document.getElementById('app').removeEventListener('touchstart', this.isClickOut, false) // touchstartchlidrens
  },
  // beforeDestroy() {
  //     console.log('111')
  //     document.removeEventListener("click", this.isClickOut);
  // },
  methods: {
    changeFather (fatherIndex) {
      this.typeShow = true
      this.currentFather = fatherIndex // children/
      this.fatherShow = fatherIndex
      if (fatherIndex == -1) { // 
        this.changeType()
        this.oldFaterShow = -1 // oldFaterShow,
        this.fatherShow = -1 // 
        this.currentName = null
      }
    },
    changeChild (childName, category, resourceType) {
      this.oldFaterShow = category // children
      this.fatherShow = category // 
      this.currentName = childName
      setTimeout(() => { // 
        this.typeShow = false
      }, 100)
      this.changeType(category, childName, resourceType)
    },
    changeType (category, typeName, resourceType) {
      this.$emit('changeType', {
        category, // 
        typeName, // 
        resourceType // ID
      })
    },
    isClickOut (e) {
      // alert(this.$refs.cateGoryWrap.className)
      // this.$refs.cateGoryWrap.style.display = 'none'
      try {
        e.path.forEach(item => {
          if (item == this.$refs.cateGoryWrap) {
            throw new Error('StopIteration') // catchreturn
          }
        })
      } catch (e) {
        if (e.message !== 'StopIteration') throw e
        return
      }
      this.fatherShow = this.oldFaterShow // 
      this.typeShow = false // this.$refs.cateGoryWrapreturn,false

      // var flag = true
      // e.path.forEach(item => {
      //     if(item == this.$refs.cateGoryWrap){
      //         flag = false
      //     }
      // })
      // if(flag){
      //     this.fatherShow = this.oldFaterShow     //
      //     this.typeShow = false    //this.$refs.cateGoryWrapreturn,false
      //     alert('')
      // }
      // alert(this.typeShow)
    }
  }
}
</script>

