<template>
    <div class="address-list-wrap">
        <my-scroll
        :data="addressListData"
        class="scroll-wrap"
        ref="myScroll"
        :listenScroll="listenScroll"
        :probeType="probeType"
        @scroll="onMyScroll"
        >
            <div class="scroll-content">
                <div
                class="classify"
                ref="classIfys"
                v-for="(item,index) in addressListData"
                :key="index"
                >
                    <div class="typeName" ref="typeName">{{item.typeStr}}</div>
                    <div class="classify-content">
                        <div class="item-detail"
                        v-for="(item,index) in item.detailArr"
                        :key="index"
                        @click="onClick(item.id)"
                        >
                            <img v-lazy="`${item.img1v1Url}?param=60y60`" :key="item.img1v1Url" alt="" class="coverImg">
                            <div class="item-name">{{item.name}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </my-scroll>
         <div class="letters-wrap"
         @touchstart.stop.prevent="onTouchStart"
         @touchmove.stop.prevent="onTouchMovet"
         @touchend.stop.prevent="onTouchEnd"
         ref="lettersWrap"
         >
            <div
            class="letterStr"
            v-for="(item,index) in addressListData"
            :key="index"
            :data-index="index"
            :class="{active:currentIndex == index}"
            ref="letterItem"
            >
            {{item.typeStr == "" ? '~' : item.typeStr}}
            </div>
        </div>
        <div v-show="fixStr" class="fixLetter" ref="fixLetter">
            {{fixStr}}
        </div>
        <loading v-show="loadingStatus"></loading>
    </div>
</template>

<script>
import myScroll from 'base/myScroll'
import { getData } from 'common/js/dom'
import loading from 'base/loading'

const ANCHOR_HEIGHT = 17 // 
const TYPENAME_HEIGHT = 30 // fixLetter

export default {
  name: 'AddressList',
  props: {
    addressListData: Array,
    loadingStatus: Boolean
  },
  components: {
    myScroll,
    loading
  },
  data () {
    return {
      currentIndex: 0,
      currentEle: null,
      posY1: 0,
      posY2: 0,
      oldIndex: null, // index
      tempIndex: null, // index
      listHeight: [], // 
      lock: true,
      fixStr: ''
    }
  },
  created () { // createddata
    this.listenScroll = true // scrollscroll
    this.probeType = 3 // scrollscroll
  },
  watch: {
    tempIndex (newVal) { // indexcurrentIndex
      this.currentIndex = newVal
    },
    currentIndex () {
      this.fixStr = this.addressListData[this.currentIndex].typeStr
    },
    addressListData () {
      setTimeout(() => {
        // this.refresh()
        this.calculateHiehgt()
        this.fixStr = this.addressListData[0].typeStr
        this.$refs.myScroll.scrollTo(0, 0, 0, 'easing')
      }, 20) // 
      // this.scrollTo(this.currentIndex)
    }
  },
  methods: {
    onClick (id) {
      this.$emit('selectTarget', { id, type: 'Singer' })
    },
    onTouchStart (e) { // deta-indexscrollTo
      this.lock = false // 
      if (!getData(e.target, 'index')) {
        return
      }
      this.currentIndex = parseInt(getData(e.target, 'index'))
      this.oldIndex = this.currentIndex // indexindex
      this.currentEle = this.$refs.classIfys[this.currentIndex]
      this.posY1 = e.touches[0].pageY
      this.scrollTo(this.currentEle)
    },
    onTouchMovet (e) { // startyscrollTo
      this.lock = false // 
      this.posY2 = e.touches[0].pageY
      var offsetIndex = (this.posY2 - this.posY1) / ANCHOR_HEIGHT | 0 // Yindex
      var num = offsetIndex + this.oldIndex
      num = num < 0 ? num = 0 : num // 
      num = num >= this.$refs.letterItem.length ? this.$refs.letterItem.length - 1 : num // 
      this.tempIndex = num // indexcurrentIndex
      this.currentEle = this.$refs.classIfys[this.currentIndex]
      this.scrollTo(this.currentEle)
    },
    onTouchEnd () {
      this.lock = true // 
    },
    onMyScroll (pos) { // pos.yindex
      if (this.lock) {
        this.handleFixLetter(pos.y)
        var arr = []
        this.listHeight.forEach((item, index) => {
          if (-pos.y >= item) { // 
            arr.push(index)
          }
        })
        var num = Math.max.apply(null, arr)
        this.currentIndex = num < 0 ? 0 : num
      }
    },
    handleFixLetter (y) {
      // scrollY()fixtop-300
      // onMyScrollindex-1nextTop
      // playTopnextTop30.playTop = -y + 30
      // -30fixTop
      // fixnextTop
      var listDom = this.$refs.typeName // typeNameoffsetTop
      if (!listDom[this.currentIndex + 1]) { // Z
        return
      }
      var playTop = Math.floor(-y) + TYPENAME_HEIGHT // typeName30px
      var nextTop = listDom[this.currentIndex + 1].offsetTop // typeName
      var fixLetter = this.$refs.fixLetter
      y > 0 ? fixLetter.style.display = 'none' : fixLetter.style.display = 'block' // 
      playTop >= nextTop ? fixLetter.style.top = nextTop - playTop + 'px' : '' // typeName30PX;
      // playTop300typeName y = 300 - 30 - scrollY;
      // typeName-30,scrollYscrollyscrollY
      fixLetter.offsetTop <= -30 ? fixLetter.style.top = '0px' : '' // 
      playTop < nextTop ? fixLetter.style.top = '0px' : '' // 
      // console.log(fixLetter)
    },
    calculateHiehgt () {
      this.listHeight = []
      var eleList = this.$refs.classIfys
      var height = 0
      this.listHeight.push(height) // pushYY
      eleList.forEach((item, index) => {
        height += item.clientHeight
        this.listHeight.push(height)
      })
    },
    scrollTo (ele) {
      this.$refs.myScroll.scrollToElement(ele)
    },
    refresh () { // singerscrollrefresh
      this.$refs.myScroll.refresh()
    }
  }
}
</script>

